import React from 'react'
import Navbar from '../Comman_Layout/Header/Navbar'
import CertificateBanner from './CertificateBanner'
import CertificateMainContent from './CertificateMainContent'
import Footer from '../Comman_Layout/Footer/Footer'
import Gotop from '../Comman_Layout/Gotop'

const Certificate = () => {
  return (
    <>
        <Navbar/>
        <CertificateBanner/>
        <CertificateMainContent/>
        <Footer/>
        <Gotop/>
    </>
  )
}

export default Certificate