import React, { useState, useEffect } from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import InternetBanner from './InternetBanner'
import InternetMainContent from './InternetMainContent'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getIOTMetaData } from '../../../Redux/action/Homeaction';
const InternetOfThings = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getIOTMetaData());
    setisLoading(true);
  }, []);
  const iotmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.iotmetadataList.data : "");
  // console.log("IOT:",iotmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {iotmetadataDetail ? `${iotmetadataDetail.metatag}` : `Internet of Things`} | Alobha Technologies</title>
          <meta name="description" content={iotmetadataDetail?`${iotmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={iotmetadataDetail?`${iotmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <InternetBanner />
      <InternetMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default InternetOfThings