import React, { useState,useEffect } from 'react'
import { useLocation } from "react-router-dom"
import Navbar from '../../Comman_Layout/Header/Navbar'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import DigitalBanner from './DigitalBanner'
import DigitalMainContent from './DigitalMainContent'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getDigitalMarketingMetaData } from '../../../Redux/action/Homeaction';

const DigitalMarketing = () => {
  const { pathname } = useLocation();
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getDigitalMarketingMetaData());
    setisLoading(true);
  }, []);
  const digitalmarketingmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.digitalmarketingmetadataList.data : "");
  // console.log("Digital Marketing:",digitalmarketingmetadataDetail);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {digitalmarketingmetadataDetail ? `${digitalmarketingmetadataDetail.metatag}` : `Digital Marketing`} | Alobha Technologies</title>
          <meta name="description" content={digitalmarketingmetadataDetail?`${digitalmarketingmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={digitalmarketingmetadataDetail?`${digitalmarketingmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <DigitalBanner />
      <DigitalMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default DigitalMarketing