import React from 'react'
import DashGlobalBanner from './DashGlobalBanner'
import DashGlobalMainContent from './DashGlobalMainContent'
import Navbar from '../../Comman_Layout/Header/Navbar'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'

const DashGlobal = () => {
  return (
    <>
        <Navbar/>
        <DashGlobalBanner/>
        <DashGlobalMainContent/>
        <Footer/>
        <Gotop/>
    </>
  )
}

export default DashGlobal