import React, { useState, useContext, useEffect } from 'react';
import { browserName, browserVersion } from "react-device-detect";
import { useSelector, useDispatch } from "react-redux";
import Navbar from '../Comman_Layout/Header/Navbar';
import Banner from '../Banner/Banner';
import SoftwareDevelopment from '../Software_Dev_Section/SoftwareDevelopment';
import ServiceHighlights from '../Home_Page_component/ServiceHighlights';
import FeaturedSolutions from '../Home_Page_component/FeaturedSolutions';
import OurServices from '../Home_Page_component/OurServices';
import AfterServiceDD from '../Home_Page_component/AfterServiceDD';
import WhatUsersSaying from '../Home_Page_component/WhatUsersSaying';
import UnderstandUsersExpectation from '../Home_Page_component/UnderstandUsersExpectation';
import OurClient from '../Home_Page_component/OurClient';
import SubscribeSection from '../Home_Page_component/SubscribeSection';
import Footer from '../Comman_Layout/Footer/Footer';
import Gotop from '../Comman_Layout/Gotop';
import StickySocial from '../Comman_Layout/StickySocial';
import Home_certificate from '../Home_Page_component/Home_certificate';
import Swal from 'sweetalert2';
import MetaContext from '../../Context/MetaContext';
import { Helmet } from 'react-helmet-async'
// import axios from 'axios';

import { getHomePageMetaData } from '../../Redux/action/Homeaction';

const Home = () => {
  //get Data from Reducer

  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getHomePageMetaData());
    setisLoading(true);
  }, []);
  const homepagemetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.homepagemetadataList.data : "");
  // console.log('Home page meta data =', homepagemetadataDetail);

  if (browserName == "Safari" && browserVersion < 16) {
    Swal.fire(
      'Your browser is NOT supported. Please upgrade to latest version of Safari.',
      'Your current version of ' + browserName + " " + browserVersion,
      'error'
    )
  }



  // else {
  //   Swal.fire(
  //     'Your Browser is Supported',
  //     'Version of ' + browserName +" "+ browserVersion,
  //     'success'
  //   )
  // }
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {homepagemetadataDetail ? `${homepagemetadataDetail.metatag}` : `Home Page`} | Alobha Technologies</title>
          <meta name="description" content={homepagemetadataDetail?`${homepagemetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={homepagemetadataDetail?`${homepagemetadataDetail.keyword}`:""} />
        </Helmet>
      }

      <Navbar />
      <Banner />
      <SoftwareDevelopment />
      <ServiceHighlights />
      <FeaturedSolutions />
      <OurServices />
      <AfterServiceDD />
      <WhatUsersSaying />
      <UnderstandUsersExpectation />
      <OurClient />
      <Home_certificate />
      <SubscribeSection />
      <Footer />
      <Gotop />
      <StickySocial />
    </>
  )
}

export default Home