import React,{useState,useEffect} from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import Banner from './Banner'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import MainContent from './MainContent'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getWebDevelopmentMetaData } from '../../../Redux/action/Homeaction';
const WebDevelopment = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getWebDevelopmentMetaData());
    setisLoading(true);
  }, []);
  const webdevelopmentmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.webdevelopmentmetadataList.data : "");
  // console.log("web development:",webdevelopmentmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {webdevelopmentmetadataDetail ? `${webdevelopmentmetadataDetail.metatag}` : `Web Development`} | Alobha Technologies</title>
          <meta name="description" content={webdevelopmentmetadataDetail?`${webdevelopmentmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={webdevelopmentmetadataDetail?`${webdevelopmentmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <Banner />
      <MainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default WebDevelopment