import React,{useState,useEffect} from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import IVRBanner from './IVRBanner'
import IVRMainContent from './IVRMainContent'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getIVRMetaData } from '../../../Redux/action/Homeaction';
const IVRServiceProvider = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getIVRMetaData());
    setisLoading(true);
  }, []);
  const ivrmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.ivrmetadataList.data : "");
  // console.log("IVR:",ivrmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {ivrmetadataDetail ? `${ivrmetadataDetail.metatag}` : `IVR Service`} | Alobha Technologies</title>
          <meta name="description" content={ivrmetadataDetail?`${ivrmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={ivrmetadataDetail?`${ivrmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <IVRBanner />
      <IVRMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default IVRServiceProvider