import React, { useState, useEffect } from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar';
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import MobileBanner from './MobileBanner';
import MobileMainContent from './MobileMainContent';
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getMobileAppDevelopmentMetaData } from '../../../Redux/action/Homeaction';
const MobileAppDevelopment = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getMobileAppDevelopmentMetaData());
    setisLoading(true);
  }, []);
  const mobileappdevelopmentmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.mobileappdevelopmentmetadataList.data : "");
  // console.log("Mobile App:",mobileappdevelopmentmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {mobileappdevelopmentmetadataDetail ? `${mobileappdevelopmentmetadataDetail.metatag}` : `Mobile App Development`} | Alobha Technologies</title>
          <meta name="description" content={mobileappdevelopmentmetadataDetail?`${mobileappdevelopmentmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={mobileappdevelopmentmetadataDetail?`${mobileappdevelopmentmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <MobileBanner />
      <MobileMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default MobileAppDevelopment