import React from 'react'
import ShapeAnimation from '../../ShapeAnimation'
import { Link } from 'react-router-dom';
const boxShadow = {
  boxShadow: '0px 3px 6px rgba(0, 0, 0, 0.16)'
}
const marginBottom = {
  marginBottom:0,
}

const BecomePartnerBanner = () => {
  return (
    <>
      <div className="become-partner-banner">
        <div className="d-table">
          <div className="d-table-cell">
            <div className="container">
              {/* <h2 class="banner-heading">Career</h2> */}
            </div>
          </div>
        </div>
        <ShapeAnimation />
      </div>
      <div className="bread-internal-cls" style={boxShadow}>
        <div className="container">
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb" style={marginBottom}>
              <li className="breadcrumb-item">
                <Link to="/">Home</Link>
              </li>
              <li className="breadcrumb-item">
                <Link to="/services">Services</Link>
              </li>
              <li className="breadcrumb-item active" aria-current="page">
                Become a Partner
              </li>
            </ol>
          </nav>
        </div>
      </div>

    </>
  )
}

export default BecomePartnerBanner