require("dotenv").config();
let host = "https://tapi.alobhatech.com";

export const getHomePageMetaData = () => {
    return async function (dispatch) {
        await fetch(`${host}/get-home-page-meta-data`, { method: "GET" })
            .then((res) => {
                res.json().then((metaData) => {
                    dispatch(setHomePageMetaData(metaData));
                    // console.log("Home meta Data in dispatch action:", metaData);
                })
            }).catch((err)=>{
                console.log("ERROR:",err.message,err);
            })

    }
}

export const setHomePageMetaData = (metaData) => {
    if (metaData) {
        return {
            type: "SET_HOME_PAGE_METADATA",
            payload: metaData,
        }
    }
}


