import React, { useState, useEffect } from 'react'
import Navbar from '../Comman_Layout/Header/Navbar'
import Footer from '../Comman_Layout/Footer/Footer'
import Gotop from '../Comman_Layout/Gotop'
import PortfolioBanner from './PortfolioBanner'
import MainArea from './MainArea'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getPortfolioMetaData } from '../../Redux/action/Homeaction';
const Portfolio = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getPortfolioMetaData());
    setisLoading(true);
  }, []);
  const portfoliometadataDetail = useSelector((state) =>state.HomeReducer ? state.HomeReducer.portfoliometadataList.data : "");
  // console.log("Portfolio:",portfoliometadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {portfoliometadataDetail ? `${portfoliometadataDetail.metatag}` : `Portfolio`} | Alobha Technologies</title>
          <meta name="description" content={portfoliometadataDetail?`${portfoliometadataDetail.metadescription}`:""} />
          <meta name="keywords" content={portfoliometadataDetail?`${portfoliometadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <PortfolioBanner />
      <MainArea />
      <Footer />
      <Gotop />
    </>
  )
}

export default Portfolio