import Gotop from '../../Comman_Layout/Gotop'
import PowerPointsBanner from './PowerPointsBanner'
import PowerPointsMainContant from './PowerPointsMainContant'


const PowerPoints = () => {
  return (
    <>
        <Navbar/>
        <PowerPointsBanner/>
        <PowerPointsMainContant/>
        <Footer/>
        <Gotop/>
    </>
  )
}

export default PowerPoints