import Gotop from '../../Comman_Layout/Gotop'
import JunkRemovalBanner from './JunkRemovalBanner'
import JunkRemovalMainContent from './JunkRemovalMainContent'

const JunkRemoval = () => {
  return (
    <>
        <Navbar/>
        <JunkRemovalBanner/>
        <JunkRemovalMainContent/>
        <Footer/>
        <Gotop/>
    </>
  )
}

export default JunkRemoval