import React, { useState, useEffect } from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import OutsourcingBanner from './OutsourcingBanner'
import OutsourcingMainContant from './OutsourcingMainContant'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getOutsourcingSoftwareDevelopmentMetaData } from '../../../Redux/action/Homeaction';
const OutsourcingSoftwareDevelopment = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getOutsourcingSoftwareDevelopmentMetaData());
    setisLoading(true);
  }, []);
  const outsourcingsoftwaredevelopmentmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.outsourcingsoftwaredevelopmentmetadataList.data : "");
  // console.log('outsourcing software development Detail:',outsourcingsoftwaredevelopmentmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {outsourcingsoftwaredevelopmentmetadataDetail ? `${outsourcingsoftwaredevelopmentmetadataDetail.metatag}` : `Outsourcing Software Development`} | Alobha Technologies</title>
          <meta name="description" content={outsourcingsoftwaredevelopmentmetadataDetail?`${outsourcingsoftwaredevelopmentmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={outsourcingsoftwaredevelopmentmetadataDetail?`${outsourcingsoftwaredevelopmentmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <OutsourcingBanner />
      <OutsourcingMainContant />
      <Footer />
      <Gotop />
    </>
  )
}

export default OutsourcingSoftwareDevelopment