import React,{useState,useEffect} from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import CmsBanner from './CmsBanner'
import CmsMainContent from './CmsMainContent'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getCMSMetaData } from '../../../Redux/action/Homeaction';
const CmsDevelopment = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getCMSMetaData());
    setisLoading(true);
  }, []);
  const cmsmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.cmsmetadataList.data : "");
  // console.log("CMS:",cmsmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {cmsmetadataDetail ? `${cmsmetadataDetail.metatag}` : `CMS`} | Alobha Technologies</title>
          <meta name="description" content={cmsmetadataDetail?`${cmsmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={cmsmetadataDetail?`${cmsmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <CmsBanner />
      <CmsMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default CmsDevelopment