import React, { useState, useEffect } from 'react'
import Navbar from '../Comman_Layout/Header/Navbar'
import Footer from '../Comman_Layout/Footer/Footer'
import Gotop from '../Comman_Layout/Gotop'
import CareerBannner from './CareerBannner'
import CareerInfo from './CareerInfo'
import CareerHiring from './CareerHiring'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getCareerPageMetaData } from '../../Redux/action/Homeaction';
const Career = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getCareerPageMetaData());
    setisLoading(true);
  }, []);
  const careerpagemetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.careerpagemetadataList.data : "");
  // console.log("Carrer Page:", careerpagemetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {careerpagemetadataDetail ? `${careerpagemetadataDetail.metatag}` : `Career Page`} | Alobha Technologies</title>
          <meta name="description" content={careerpagemetadataDetail?`${careerpagemetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={careerpagemetadataDetail?`${careerpagemetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <CareerBannner />
      <CareerInfo />
      <CareerHiring />
      <Footer />
      <Gotop />
    </>
  )
}

export default Career