import Refund_mainContent from './Refund_mainContent'
import Footer from '../Comman_Layout/Footer/Footer'
import Gotop from '../Comman_Layout/Gotop'

const Refund_Home = () => {
  return (
    <>
        <Navbar/>
        <Refund_Banner/>
        <Refund_mainContent/>
        <Footer/>
        <Gotop/>
    </>
  )
}

export default Refund_Home