import React,{useState,useEffect} from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import ErpBanner from './ErpBanner'
import ErpMainContent from './ErpMainContent'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getERPMetaData } from '../../../Redux/action/Homeaction';

const erp = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getERPMetaData());
    setisLoading(true);
  }, []);
  const erpmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.erpmetadataList.data : "");
  // console.log('ERP:',erpmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {erpmetadataDetail ? `${erpmetadataDetail.metatag}` : `ERP`} | Alobha Technologies</title>
          <meta name="description" content={erpmetadataDetail?`${erpmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={erpmetadataDetail?`${erpmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <ErpBanner />
      <ErpMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default erp