import React, { useRef, useState } from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import $ from 'jquery';
import WhatUsersSayingObj from './WhatUsersSayingObj';
import WhatUsersSayingObjslider2 from './WhatUsersSayingObjslider2';
import ShapeAnimation from '../../ShapeAnimation';
const WhatUsersSaying = () => {
  const settings1 = {
    arrows: false,
    nav: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    cssEase: 'linear',
    fade: true,
    lazyLoad: true,
    autoplay: true,
    draggable: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: false
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };

  function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <button className="next-arrow slick-arrow" >
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-arrow-right"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg>
        </button>
      </div>
    );
  }

  function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div className={className}
        onClick={onClick}>
        <button className="prev-arrow slick-arrow">
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-arrow-left"><line x1="19" y1="12" x2="5" y2="12"></line><polyline points="12 19 5 12 12 5"></polyline></svg>
        </button>
      </div>
    );
  }
  // const customeSlider = useState();

  const settings2 = {
    // dots: true,
    // arrows: true,
    nav: true,
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
    cssEase: 'linear',
    autoplay: true,
    lazyLoad: true,
    centerMode: true,
    draggable: true,
    focusOnSelect: true,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };

  const [nav1, setNav1] = useState();
  const [nav2, setNav2] = useState();
  return (
    <div>
      <section className="feedback-area pt-60 pb-60 bg-f7fafd feedback-cus">
        <div className="container">
          <div className="section-title">
            <h2>What users Saying</h2>
            <div className="bar" />
            <p>
              We love what we do and we do what our clients love &amp; work with great
              clients all over the world to create thoughtful and purposeful websites.
            </p>
          </div>
          <div className="feedback-slides">
            <div className="client-feedback">
              <Slider {...settings1} asNavFor={nav2} ref={(slider1) => setNav1(slider1)}>
                {WhatUsersSayingObj.map((wusrepeat) => {
                  return <div className="item" key={wusrepeat.id}>
                    <div className="single-feedback">
                      <div className="client-img">
                        <img src={wusrepeat.img} alt="user" loading="lazy" />
                      </div>
                      <h3 className="pb-2">{wusrepeat.heading}</h3>
                      <p style={{ display: "inline" }}>
                        <img src={wusrepeat.p1img} loading="lazy" alt="user" style={{ display: "inline", height: 25 }} />
                        {wusrepeat.para}
                        <img src={wusrepeat.p2img} style={{ display: "inline", height: 25 }} alt="user" loading="lazy" />
                      </p>
                    </div>
                  </div>
                })}

              </Slider>
            </div>
            <div className="client-thumbnails">
              <Slider {...settings2} asNavFor={nav1} ref={(slider2) => setNav2(slider2)} slidesToShow={5} swipeToSlide={true} focusOnSelect={true}>
                {WhatUsersSayingObjslider2.map((wusslider2) => {
                  return <div className="item" key={wusslider2.id}>
                    <div className="img-fill">
                      <img src={wusslider2.img} alt="client" loading="lazy" />
                    </div>
                  </div>
                })}
              </Slider>
            </div>

          </div>
        </div>
      <ShapeAnimation/>
      </section>

    </div>
  )
}

export default WhatUsersSaying