import React from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import BluetoothBanner from './BluetoothBanner'
import BluetoothMainContent from './BluetoothMainContent'

const BluetoothMain = () => {
  return (
    <>
      <Navbar/>
      <BluetoothBanner/>
      <BluetoothMainContent/>
      <Footer/>
      <Gotop/>
    </>
  )
}

export default BluetoothMain