import React, { useState, useEffect } from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import MachineBanner from './MachineBanner'
import MachineMainContent from './MachineMainContent'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getMLMetaData } from '../../../Redux/action/Homeaction';
const MachineLearning = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getMLMetaData());
    setisLoading(true);
  }, []);
  const mlmetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.mlmetadataList.data : "");
  // console.log("ML:",mlmetadataDetail);
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {mlmetadataDetail ? `${mlmetadataDetail.metatag}` : `Machine Learning`} | Alobha Technologies</title>
          <meta name="description" content={mlmetadataDetail?`${mlmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={mlmetadataDetail?`${mlmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <MachineBanner />
      <MachineMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default MachineLearning