import React, { useEffect, useState } from 'react'
import Navbar from '../Comman_Layout/Header/Navbar'
import InnerBanner from './InnerBanner'
import GetInTouch from './GetInTouch'
import Footer from '../Comman_Layout/Footer/Footer'
import Gotop from '../Comman_Layout/Gotop'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getContactusPageMetaData } from '../../Redux/action/Homeaction';
const ContactUs = () => {
    const dispatch = useDispatch();
    const [isLoading, setisLoading] = useState(false);
    useEffect(() => {
        setisLoading(false);
        dispatch(getContactusPageMetaData());
        setisLoading(true);
    }, []);
    const contactuspagemetadataDetail = useSelector((state) => state.HomeReducer ? state.HomeReducer.contactuspagemetadataList.data : "");
    console.log("contactus:", contactuspagemetadataDetail);
    return (
        <>
            {
                isLoading && <Helmet>
                    <title> {contactuspagemetadataDetail ? `${contactuspagemetadataDetail.metatag}` : `Contact Us Page`} | Alobha Technologies</title>
                    <meta name="description" content={contactuspagemetadataDetail?`${contactuspagemetadataDetail.metadescription}`:""} />
                    <meta name="keywords" content={contactuspagemetadataDetail?`${contactuspagemetadataDetail.keyword}`:""} />
                </Helmet>
            }
            <Navbar />
            <InnerBanner />
            <GetInTouch />
            <Footer />
            <Gotop />
        </>
    )
}

export default ContactUs