import Privacy_mainContent from './Privacy_mainContent'
import Footer from '../Comman_Layout/Footer/Footer'
import Gotop from '../Comman_Layout/Gotop'

const Privacy_home = () => {
  return (
    <>
        <Navbar/>
        <Privacy_banner/>
        <Privacy_mainContent/>
        <Footer/>
        <Gotop/>
    </>
  )
}

export default Privacy_home