import React,{useState,useEffect} from 'react'
import Navbar from '../../Comman_Layout/Header/Navbar'
import DotnetBanner from './DotnetBanner'
import DotnetMainContent from './DotnetMainContent'
import Footer from '../../Comman_Layout/Footer/Footer'
import Gotop from '../../Comman_Layout/Gotop'
import { Helmet } from 'react-helmet-async'
import { useSelector, useDispatch } from "react-redux";
import { getDotNetMetaData } from '../../../Redux/action/Homeaction';

const DotnetDevelopment = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);
  useEffect(() => {
    setisLoading(false);
    dispatch(getDotNetMetaData());
    setisLoading(true);
  }, []);
  const dotnetmetadataDetail = useSelector((state) =>state.HomeReducer ? state.HomeReducer.dotnetmetadataList.data : "");
  // console.log("DOT NET:",dotnetmetadataDetail)
  return (
    <>
      {
        isLoading && <Helmet>
          <title> {dotnetmetadataDetail ? `${dotnetmetadataDetail.metatag}` : `Dot Net Technology Development`} | Alobha Technologies</title>
          <meta name="description" content={dotnetmetadataDetail?`${dotnetmetadataDetail.metadescription}`:""} />
          <meta name="keywords" content={dotnetmetadataDetail?`${dotnetmetadataDetail.keyword}`:""} />
        </Helmet>
      }
      <Navbar />
      <DotnetBanner />
      <DotnetMainContent />
      <Footer />
      <Gotop />
    </>
  )
}

export default DotnetDevelopment