import Gotop from '../Comman_Layout/Gotop'
import MainSection from './MainSection'
import InsideBanner from './InsideBanner'

const GetaQuote = () => {
    return (
        <>
            <Navbar />
            <InsideBanner/>
            <MainSection/>
            <Footer/>
            <Gotop/>
        </>
    )
}

export default GetaQuote